
.g-modal-wrapper {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: -20px;
  text-align: center;
}
.g-modal-wrapper.private {
    position: absolute;
}
.g-modal-wrapper.g-modal-fade-enter-active {
    animation: g-modal-fade-in .3s;
}
.g-modal-wrapper.g-modal-fade-leave-active {
    animation: g-modal-fade-out .3s;
}
@keyframes g-modal-fade-in {
0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes g-modal-fade-out {
0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
}
.g-modal-wrapper::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle;
}
.g-modal-wrapper .g-modal {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: #fff;
    border: 1px solid #c2c7d1;
    box-shadow: 0 4px 10px 1px rgba(48, 59, 70, 0.2);
    border-radius: 4px;
    position: relative;
}
.g-modal-wrapper .g-modal .g-modal-header {
      height: 60px;
      line-height: 60px;
      font-size: 18px;
      color: #2c3e50;
      padding: 0 20px;
      border-bottom: 1px solid #e6e9f0;
}
.g-modal-wrapper .g-modal .g-modal-header > i {
        float: right;
        line-height: 20px;
        margin-top: 20px;
        cursor: pointer;
}
.g-modal-wrapper .g-modal .g-modal-header > i:hover {
          color: #20A0FF;
}
.g-modal-wrapper .g-modal .g-modal-content {
      padding: 20px;
}
.g-modal-wrapper .g-modal .g-modal-footer {
      padding: 0 20px 20px;
}
.g-global-popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: .5;
  background: #000;
}
.g-global-popup.private {
    position: absolute;
}
.g-global-popup.g-global-popup-enter {
    animation: g-global-popup-in .2s ease;
}
.g-global-popup.g-global-popup-leave {
    animation: g-global-popup-out .2s ease forwards;
}
@keyframes g-global-popup-in {
0% {
    opacity: 0;
}
}
@keyframes g-global-popup-out {
100% {
    opacity: 0;
}
}
.g-color-picker {
  position: relative;
  width: 230px;
  box-sizing: initial;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15), 0 8px 16px rgba(0, 0, 0, 0.15);
  overflow: hidden;
}
.g-color-picker .g-color-picker-saturation-wrap {
    width: 100%;
    height: 150px;
    position: relative;
    overflow: hidden;
}
.g-color-picker .g-color-picker-controls {
    position: relative;
    padding: 10px 44px 10px 10px;
}
.g-color-picker .g-color-picker-controls .g-color-picker-sliders {
      padding: 4px 0;
}
.g-color-picker .g-color-picker-controls .g-color-picker-sliders .g-color-picker-hue-wrap {
        position: relative;
        height: 10px;
}
.g-color-picker .g-color-picker-controls .g-color-picker-sliders .g-color-picker-hue-wrap.single {
          margin: 7px 0;
}
.g-color-picker .g-color-picker-controls .g-color-picker-sliders .g-color-picker-alpha-wrap {
        position: relative;
        height: 10px;
        margin-top: 4px;
}
.g-color-picker .g-color-picker-controls .g-color-picker-color-wrap {
      width: 24px;
      height: 24px;
      position: absolute;
      top: 10px;
      right: 10px;
      margin-top: 4px;
      margin-left: 4px;
      border-radius: 3px;
}
.g-color-picker .g-color-picker-controls .g-color-picker-color-wrap .g-color-picker-active-color {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 2px;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15), inset 0 0 4px rgba(0, 0, 0, 0.25);
        z-index: 2;
}
.g-color-picker .g-color-picker-presets {
    padding: 10px;
    border-top: 1px solid #ddd;
    text-align: left;
}
.g-color-picker .g-color-picker-presets .g-color-picker-presets-color-wrap {
      border-radius: 2px;
      overflow: hidden;
      position: relative;
      display: inline-block;
      margin: 0 5px 5px 0;
      vertical-align: top;
      cursor: pointer;
      width: 16px;
      height: 16px;
}
.g-color-picker .g-color-picker-presets .g-color-picker-presets-color-wrap .g-color-picker-presets-color {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15);
}



