
.g-modal-wrapper {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: -20px;
  text-align: center;
}
.g-modal-wrapper.private {
    position: absolute;
}
.g-modal-wrapper.g-modal-fade-enter-active {
    animation: g-modal-fade-in .3s;
}
.g-modal-wrapper.g-modal-fade-leave-active {
    animation: g-modal-fade-out .3s;
}
@keyframes g-modal-fade-in {
0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes g-modal-fade-out {
0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
}
.g-modal-wrapper::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle;
}
.g-modal-wrapper .g-modal {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: #fff;
    border: 1px solid #c2c7d1;
    box-shadow: 0 4px 10px 1px rgba(48, 59, 70, 0.2);
    border-radius: 4px;
    position: relative;
}
.g-modal-wrapper .g-modal .g-modal-header {
      height: 60px;
      line-height: 60px;
      font-size: 18px;
      color: #2c3e50;
      padding: 0 20px;
      border-bottom: 1px solid #e6e9f0;
}
.g-modal-wrapper .g-modal .g-modal-header > i {
        float: right;
        line-height: 20px;
        margin-top: 20px;
        cursor: pointer;
}
.g-modal-wrapper .g-modal .g-modal-header > i:hover {
          color: #20A0FF;
}
.g-modal-wrapper .g-modal .g-modal-content {
      padding: 20px;
}
.g-modal-wrapper .g-modal .g-modal-footer {
      padding: 0 20px 20px;
}
.g-global-popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: .5;
  background: #000;
}
.g-global-popup.private {
    position: absolute;
}
.g-global-popup.g-global-popup-enter {
    animation: g-global-popup-in .2s ease;
}
.g-global-popup.g-global-popup-leave {
    animation: g-global-popup-out .2s ease forwards;
}
@keyframes g-global-popup-in {
0% {
    opacity: 0;
}
}
@keyframes g-global-popup-out {
100% {
    opacity: 0;
}
}
.g-select {
  width: 200px;
  height: 30px;
  line-height: 28px;
  position: relative;
  display: inline-block;
  border-radius: 2px;
  border: 1px solid #d1dbe5;
  font-size: 13px;
  padding: 0 10px;
  transition: border .2s;
  color: #1f2d3d;
}
.g-select.small {
    height: 26px;
    line-height: 24px;
}
.g-select.large {
    height: 34px;
    line-height: 32px;
}
.g-select:hover {
    border-color: #8391a5;
}
.g-select.focus {
    border: 1px solid #20a0ff !important;
}
.g-select.is-disabled {
    cursor: not-allowed;
    background-color: #eef1f6;
    border-color: #d1dbe5;
    color: #bbb;
}
.g-select.is-disabled:hover {
      border-color: #d1dbe5;
}
.g-select .g-select-label-warp {
    width: 100%;
    height: 100%;
    cursor: pointer;
}
.g-select .g-select-label-warp > i {
      color: #bfcbd9;
      font-size: 14px;
      position: absolute;
      top: 50%;
      right: 8px;
      transform: translateY(-50%);
      margin-top: 0;
}
.g-select .g-select-list-warp {
    position: absolute;
    top: 30px;
    line-height: 30px;
    left: 0;
    z-index: 100;
    background: #FFF;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.1), 0 10px 12px 0 rgba(170, 182, 206, 0.36);
    padding: 6px 0;
    width: 100%;
    overflow: hidden;
    overflow-y: auto;
}
.g-select .g-select-list-warp .g-select-list {
      width: 100%;
}
.g-select .g-select-list-warp .g-select-list .g-select-list-item {
        display: block;
        height: 32px;
        padding: 0 16px;
        white-space: nowrap;
        color: rgba(10, 18, 32, 0.64);
        cursor: pointer;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
}
.g-select .g-select-list-warp .g-select-list .g-select-list-item:hover {
          color: rgba(10, 18, 32, 0.87);
          background-color: rgba(232, 235, 237, 0.6);
}
.g-select .g-select-list-warp .g-select-list .g-select-list-item.active {
          color: #5182E4;
}



