
.g-modal-wrapper {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: -20px;
  text-align: center;
}
.g-modal-wrapper.private {
    position: absolute;
}
.g-modal-wrapper.g-modal-fade-enter-active {
    animation: g-modal-fade-in .3s;
}
.g-modal-wrapper.g-modal-fade-leave-active {
    animation: g-modal-fade-out .3s;
}
@keyframes g-modal-fade-in {
0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes g-modal-fade-out {
0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
}
.g-modal-wrapper::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle;
}
.g-modal-wrapper .g-modal {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: #fff;
    border: 1px solid #c2c7d1;
    box-shadow: 0 4px 10px 1px rgba(48, 59, 70, 0.2);
    border-radius: 4px;
    position: relative;
}
.g-modal-wrapper .g-modal .g-modal-header {
      height: 60px;
      line-height: 60px;
      font-size: 18px;
      color: #2c3e50;
      padding: 0 20px;
      border-bottom: 1px solid #e6e9f0;
}
.g-modal-wrapper .g-modal .g-modal-header > i {
        float: right;
        line-height: 20px;
        margin-top: 20px;
        cursor: pointer;
}
.g-modal-wrapper .g-modal .g-modal-header > i:hover {
          color: #20A0FF;
}
.g-modal-wrapper .g-modal .g-modal-content {
      padding: 20px;
}
.g-modal-wrapper .g-modal .g-modal-footer {
      padding: 0 20px 20px;
}
.g-global-popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: .5;
  background: #000;
}
.g-global-popup.private {
    position: absolute;
}
.g-global-popup.g-global-popup-enter {
    animation: g-global-popup-in .2s ease;
}
.g-global-popup.g-global-popup-leave {
    animation: g-global-popup-out .2s ease forwards;
}
@keyframes g-global-popup-in {
0% {
    opacity: 0;
}
}
@keyframes g-global-popup-out {
100% {
    opacity: 0;
}
}
.g-color-bar-picker {
  width: 230px;
  position: relative;
  z-index: 10;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15), 0 8px 16px rgba(0, 0, 0, 0.15);
}
.g-color-bar-picker .color-bar-icon {
    position: absolute;
    right: 10px;
    top: 8px;
    display: block;
    text-align: center;
    width: 20px;
    height: 20px;
    line-height: 20px;
    color: #20A0FF;
    cursor: pointer;
}
.g-color-bar-picker .color-bar-panel-list {
    width: 100%;
    position: relative;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-current {
      width: 100%;
      border-bottom: 1px solid #ddd;
      background: #f2f6f9;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-current.alpha .color-bar {
        height: 26px;
        padding-bottom: 0;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-current .color-bar-alpha {
        width: 100%;
        height: 6px;
        position: relative;
        padding: 0 16px;
        margin: 10px 0;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-current .color-bar-alpha .g-color-picker-c-alpha {
          position: relative;
          width: 100%;
          height: 100%;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box {
      width: 100%;
      height: 185px;
      background: #fff;
      overflow: hidden;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper {
        display: block;
        width: 100%;
        height: 148px;
        overflow: hidden;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper .vue-scrollbar__scrollbar-vertical {
          width: 4px;
          opacity: 0.4;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper .vue-scrollbar__scrollbar-vertical:hover {
            opacity: 1;
            width: 10px;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper .color-bar-list {
          display: block;
          width: 100%;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper .color-bar-list > li {
            cursor: pointer;
            display: block;
            position: relative;
            border-bottom: 1px solid #eee;
            padding-right: 22px;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper .color-bar-list > li > i {
              display: none;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper .color-bar-list > li:hover {
              background: rgba(32, 160, 255, 0.1);
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper .color-bar-list > li:hover > i {
                display: block;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .color-bar-list-wrapper .color-bar-list > li.active::before {
              content: '';
              display: block;
              position: absolute;
              top: 9px;
              bottom: 9px;
              left: 10px;
              right: 32px;
              border: 1px solid rgba(17, 129, 251, 0.64);
              border-radius: 8px;
              background: rgba(17, 129, 251, 0.08);
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .custom-color-bar {
        position: relative;
        height: 36px;
        line-height: 36px;
        font-size: 12px;
        text-align: center;
        color: #20A0FF;
        cursor: pointer;
        border-top: 1px solid #ddd;
        padding-right: 22px;
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .custom-color-bar .custom-color-bar-box.active::before {
          content: '';
          display: block;
          position: absolute;
          top: 9px;
          bottom: 9px;
          left: 10px;
          right: 32px;
          border: 1px solid rgba(17, 129, 251, 0.64);
          border-radius: 8px;
          background: rgba(17, 129, 251, 0.08);
}
.g-color-bar-picker .color-bar-panel-list .color-bar-list-box .custom-color-bar > p {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
}
.g-color-bar-picker .color-bar-panel-edit .color-bar-current {
    position: relative;
    width: 100%;
    border-bottom: 1px solid #ddd;
    background: #fff;
    padding-right: 22px;
}
.g-color-bar-picker .color-bar-panel-edit .color-bar-current .edit-pointer {
      height: 1px;
      background: #000;
      position: absolute;
      bottom: 0;
}
.g-color-bar-picker .color-bar-panel-edit .color-bar-current .edit-confirm {
      position: absolute;
      right: 10px;
      top: 8px;
      display: block;
      text-align: center;
      width: 20px;
      height: 20px;
      line-height: 20px;
      color: #20A0FF;
      cursor: pointer;
}



