
.g-modal-wrapper {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: -20px;
  text-align: center;
}
.g-modal-wrapper.private {
    position: absolute;
}
.g-modal-wrapper.g-modal-fade-enter-active {
    animation: g-modal-fade-in .3s;
}
.g-modal-wrapper.g-modal-fade-leave-active {
    animation: g-modal-fade-out .3s;
}
@keyframes g-modal-fade-in {
0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes g-modal-fade-out {
0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
}
.g-modal-wrapper::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle;
}
.g-modal-wrapper .g-modal {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: #fff;
    border: 1px solid #c2c7d1;
    box-shadow: 0 4px 10px 1px rgba(48, 59, 70, 0.2);
    border-radius: 4px;
    position: relative;
}
.g-modal-wrapper .g-modal .g-modal-header {
      height: 60px;
      line-height: 60px;
      font-size: 18px;
      color: #2c3e50;
      padding: 0 20px;
      border-bottom: 1px solid #e6e9f0;
}
.g-modal-wrapper .g-modal .g-modal-header > i {
        float: right;
        line-height: 20px;
        margin-top: 20px;
        cursor: pointer;
}
.g-modal-wrapper .g-modal .g-modal-header > i:hover {
          color: #20A0FF;
}
.g-modal-wrapper .g-modal .g-modal-content {
      padding: 20px;
}
.g-modal-wrapper .g-modal .g-modal-footer {
      padding: 0 20px 20px;
}
.g-global-popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: .5;
  background: #000;
}
.g-global-popup.private {
    position: absolute;
}
.g-global-popup.g-global-popup-enter {
    animation: g-global-popup-in .2s ease;
}
.g-global-popup.g-global-popup-leave {
    animation: g-global-popup-out .2s ease forwards;
}
@keyframes g-global-popup-in {
0% {
    opacity: 0;
}
}
@keyframes g-global-popup-out {
100% {
    opacity: 0;
}
}
.g-breadcrumb {
  margin-bottom: 12px;
}
.g-breadcrumb::after {
    content: '';
    display: block;
    visibility: hidden;
    clear: both;
}
.g-breadcrumb .g-breadcrumb-item {
    font-size: 16px;
    color: #2c3e50;
    font-weight: 600;
    float: left;
    letter-spacing: 0;
    height: 25px;
    line-height: 25px;
}
.g-breadcrumb .g-breadcrumb-item.active {
      color: #889;
      font-weight: 300;
}
.g-breadcrumb .g-breadcrumb-separator {
    float: left;
    height: 25px;
    line-height: 25px;
    margin: 0 10px;
}
.g-breadcrumb .g-breadcrumb-separator > i {
      font-size: 16px;
}



