
.g-color-picker-c-alpha {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  border-radius: 2px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.g-color-picker-c-alpha-checkboard-wrap {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}
.g-color-picker-c-alpha-gradient {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}
.g-color-picker-c-alpha-container {
  cursor: pointer;
  position: relative;
  z-index: 2;
  height: 100%;
  margin: 0 3px;
}
.g-color-picker-c-alpha-pointer {
  z-index: 2;
  position: absolute;
}
.g-color-picker-c-alpha-picker {
  cursor: pointer;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  box-shadow: 0 0 4px black;
  background: #fff;
  margin-left: -6px;
  margin-top: -2px;
}
.g-color-picker-c-alpha.small .g-color-picker-c-alpha-picker {
    height: 8px;
    width: 8px;
    margin-left: -4px;
}



