
.g-modal-wrapper {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: -20px;
  text-align: center;
}
.g-modal-wrapper.private {
    position: absolute;
}
.g-modal-wrapper.g-modal-fade-enter-active {
    animation: g-modal-fade-in .3s;
}
.g-modal-wrapper.g-modal-fade-leave-active {
    animation: g-modal-fade-out .3s;
}
@keyframes g-modal-fade-in {
0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes g-modal-fade-out {
0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
}
.g-modal-wrapper::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle;
}
.g-modal-wrapper .g-modal {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: #fff;
    border: 1px solid #c2c7d1;
    box-shadow: 0 4px 10px 1px rgba(48, 59, 70, 0.2);
    border-radius: 4px;
    position: relative;
}
.g-modal-wrapper .g-modal .g-modal-header {
      height: 60px;
      line-height: 60px;
      font-size: 18px;
      color: #2c3e50;
      padding: 0 20px;
      border-bottom: 1px solid #e6e9f0;
}
.g-modal-wrapper .g-modal .g-modal-header > i {
        float: right;
        line-height: 20px;
        margin-top: 20px;
        cursor: pointer;
}
.g-modal-wrapper .g-modal .g-modal-header > i:hover {
          color: #20A0FF;
}
.g-modal-wrapper .g-modal .g-modal-content {
      padding: 20px;
}
.g-modal-wrapper .g-modal .g-modal-footer {
      padding: 0 20px 20px;
}
.g-global-popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: .5;
  background: #000;
}
.g-global-popup.private {
    position: absolute;
}
.g-global-popup.g-global-popup-enter {
    animation: g-global-popup-in .2s ease;
}
.g-global-popup.g-global-popup-leave {
    animation: g-global-popup-out .2s ease forwards;
}
@keyframes g-global-popup-in {
0% {
    opacity: 0;
}
}
@keyframes g-global-popup-out {
100% {
    opacity: 0;
}
}
.g-message-box-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
}
.g-message-box-wrapper::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle;
}
.g-message-box-wrapper.private {
    position: absolute;
}
.g-message-box-wrapper .g-message-box {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: #fff;
    width: 420px;
    border-radius: 3px;
    font-size: 16px;
    overflow: hidden;
}
@media screen and (max-width: 450px) {
.g-message-box-wrapper .g-message-box {
        width: 90%;
}
}
.g-message-box-wrapper .g-message-box .g-message-box-header {
      position: relative;
      padding: 20px 20px 0;
}
.g-message-box-wrapper .g-message-box .g-message-box-header .g-message-box-close {
        display: inline-block;
        position: absolute;
        top: 19px;
        right: 18px;
        color: #999;
        cursor: pointer;
        line-height: 20px;
        text-align: center;
        font-size: 20px;
}
.g-message-box-wrapper .g-message-box .g-message-box-header .g-message-box-close:hover {
          color: #20a0ff;
}
.g-message-box-wrapper .g-message-box .g-message-box-header .g-message-box-close.is-disabled, .g-message-box-wrapper .g-message-box .g-message-box-header .g-message-box-close.is-disabled:hover {
          color: #bfcbd9;
          cursor: not-allowed;
}
.g-message-box-wrapper .g-message-box .g-message-box-header .g-message-box-title {
        padding-left: 0;
        margin-bottom: 0;
        font-size: 16px;
        font-weight: 700;
        height: 18px;
        color: #333;
}
.g-message-box-wrapper .g-message-box .g-message-box-content {
      padding: 30px 20px;
      color: #48576a;
      font-size: 14px;
      position: relative;
}
.g-message-box-wrapper .g-message-box .g-message-box-content .g-message-box-message {
        margin: 0;
}
.g-message-box-wrapper .g-message-box .g-message-box-content .g-message-box-message p {
          margin: 0;
          line-height: 1.4;
          word-break: break-all;
}
.g-message-box-wrapper .g-message-box .g-message-box-content .g-message-box-status {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
}
.g-message-box-wrapper .g-message-box .g-message-box-content .g-message-box-status > i {
          font-size: 36px !important;
}
.g-message-box-wrapper .g-message-box .g-message-box-content .g-message-box-status.info {
          color: #20A0FF;
}
.g-message-box-wrapper .g-message-box .g-message-box-content .g-message-box-status.success {
          color: #3dbf74;
}
.g-message-box-wrapper .g-message-box .g-message-box-content .g-message-box-status.warning {
          color: #fd9827;
}
.g-message-box-wrapper .g-message-box .g-message-box-content .g-message-box-status.error {
          color: #d9534f;
}
.g-message-box-wrapper .g-message-box .g-message-box-footer {
      padding: 10px 20px 15px;
      text-align: right;
}
.g-message-box-wrapper .g-message-box .g-message-box-footer button:nth-child(2) {
        margin-left: 10px;
}
.g-message-box-wrapper.g-message-box-fade-enter-active {
    animation: g-message-box-fade-in .3s;
}
.g-message-box-wrapper.g-message-box-fade-leave-active {
    animation: g-message-box-fade-out .3s;
}
@keyframes g-message-box-fade-in {
0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes g-message-box-fade-out {
0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
}



