
.g-modal-wrapper {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: -20px;
  text-align: center;
}
.g-modal-wrapper.private {
    position: absolute;
}
.g-modal-wrapper.g-modal-fade-enter-active {
    animation: g-modal-fade-in .3s;
}
.g-modal-wrapper.g-modal-fade-leave-active {
    animation: g-modal-fade-out .3s;
}
@keyframes g-modal-fade-in {
0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
100% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes g-modal-fade-out {
0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
}
100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0;
}
}
.g-modal-wrapper::after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle;
}
.g-modal-wrapper .g-modal {
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: #fff;
    border: 1px solid #c2c7d1;
    box-shadow: 0 4px 10px 1px rgba(48, 59, 70, 0.2);
    border-radius: 4px;
    position: relative;
}
.g-modal-wrapper .g-modal .g-modal-header {
      height: 60px;
      line-height: 60px;
      font-size: 18px;
      color: #2c3e50;
      padding: 0 20px;
      border-bottom: 1px solid #e6e9f0;
}
.g-modal-wrapper .g-modal .g-modal-header > i {
        float: right;
        line-height: 20px;
        margin-top: 20px;
        cursor: pointer;
}
.g-modal-wrapper .g-modal .g-modal-header > i:hover {
          color: #20A0FF;
}
.g-modal-wrapper .g-modal .g-modal-content {
      padding: 20px;
}
.g-modal-wrapper .g-modal .g-modal-footer {
      padding: 0 20px 20px;
}
.g-global-popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: .5;
  background: #000;
}
.g-global-popup.private {
    position: absolute;
}
.g-global-popup.g-global-popup-enter {
    animation: g-global-popup-in .2s ease;
}
.g-global-popup.g-global-popup-leave {
    animation: g-global-popup-out .2s ease forwards;
}
@keyframes g-global-popup-in {
0% {
    opacity: 0;
}
}
@keyframes g-global-popup-out {
100% {
    opacity: 0;
}
}
.g-button {
  display: inline-block;
  white-space: nowrap;
  cursor: pointer;
  background: #f6fbff;
  border: 1px solid #bfcbd9;
  color: #58B7FF;
  border-color: #58B7FF;
  margin: 0;
  border-radius: 4px;
  padding: 0 12px;
  height: 28px;
  line-height: 26px;
  outline: none;
  font-size: 13px;
}
.g-button[class*=g-icon-] + span {
    margin-left: 5px;
}
.g-button.small {
    height: 24px;
    line-height: 22px;
}
.g-button.large {
    height: 32px;
    line-height: 30px;
}
.g-button.is-round {
    border-radius: 14px;
    padding: 0 16px;
}
.g-button.is-round.small {
      border-radius: 12px;
}
.g-button.is-round.large {
      border-radius: 16px;
}
.g-button:focus, .g-button:hover {
    color: #8bcdff;
    border-color: #8bcdff;
}
.g-button:active {
    color: #1D8CE0;
    border-color: #1D8CE0;
}
.g-button.text {
    border: none;
    color: #20a0ff;
    background: 0 0;
    padding-left: 0;
    padding-right: 0;
}
.g-button.text:focus, .g-button.text:hover {
      color: #4db3ff;
}
.g-button.text:active {
      color: #1d90e6;
}
.g-button.primary {
    color: #fff;
    background-color: #20a0ff;
    border-color: #20a0ff;
}
.g-button.primary:focus, .g-button.primary:hover {
      background: #4db3ff;
      border-color: #4db3ff;
      color: #fff;
}
.g-button.primary:active {
      background: #1d90e6;
      border-color: #1d90e6;
      color: #fff;
}
.g-button.info {
    color: #fff;
    background-color: #50bfff;
    border-color: #50bfff;
}
.g-button.info:focus, .g-button.info:hover {
      background: #73ccff;
      border-color: #73ccff;
      color: #fff;
}
.g-button.info:active {
      background: #48ace6;
      border-color: #48ace6;
      color: #fff;
}
.g-button.success {
    color: #fff;
    background-color: #13ce66;
    border-color: #13ce66;
}
.g-button.success:focus, .g-button.success:hover {
      background: #42d885;
      border-color: #42d885;
      color: #fff;
}
.g-button.success:active {
      background: #11b95c;
      border-color: #11b95c;
      color: #fff;
}
.g-button.warning {
    color: #fff;
    background-color: #f7ba2a;
    border-color: #f7ba2a;
}
.g-button.warning:focus, .g-button.warning:hover {
      background: #f9c855;
      border-color: #f9c855;
      color: #fff;
}
.g-button.warning:active {
      background: #dea726;
      border-color: #dea726;
      color: #fff;
}
.g-button.danger {
    color: #fff;
    background-color: #ff4949;
    border-color: #ff4949;
}
.g-button.danger:focus, .g-button.danger:hover {
      background: #ff6d6d;
      border-color: #ff6d6d;
      color: #fff;
}
.g-button.danger:active {
      background: #e64242;
      border-color: #e64242;
      color: #fff;
}
.g-button.is-loading {
    position: relative;
    pointer-events: none;
}
.g-button.is-loading:before {
      content: '';
      position: absolute;
      left: -1px;
      top: -1px;
      right: -1px;
      bottom: -1px;
      border-radius: inherit;
      background-color: rgba(255, 255, 255, 0.35);
}
.g-button.is-disabled, .g-button.is-disabled:focus, .g-button.is-disabled:hover {
    color: #bfcbd9;
    cursor: not-allowed;
    background-image: none;
    background-color: #eef1f6;
    border-color: #d1dbe5;
}
.g-button.is-disabled.text {
    background-color: transparent;
}



